@import url(https://fonts.googleapis.com/css2?family=Oswald&display=swap);
*{
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
  font-family: 'Oswald', sans-serif;
  font-weight: regular 400;
}

.resume{
  max-width: 766px;
  width: 50%;
  margin: auto;
  box-shadow: 5px 5px 5px 5px #BDC8E9;
}

.photo{
  max-width: 143px;
  max-height: 143px;
  padding-left: 20px;
}

.photo img{
  height: auto;
  width: 100%;
  border-radius: 50%;
}

.headerDetail img{
  max-width: 20px;
  max-height: 20px;
  box-sizing: content-box;
  padding-right: 7px;
}

.headerDetail h5{
  padding-left: 10px;
}

.headerDetail{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
}


.header{
  background-color: #152F79;
  color: #ECEDF0;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  padding: 8px;
  padding-right: 70px;
}

.body{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-around;
          justify-content: space-around;
  color: #152F79;
  
}

.body-right{
  color: #152F79;
  text-align: left;
  font-size: 15px;
  width: 65%;
  padding: 6px;
  padding-left: 10px;
  
}

.middleLine{
  min-width: 1px;
  background-color: #152F79;
  border: 2px solid #152F79;
  box-sizing: content-box;
  margin-top: 15px;
  margin-bottom: 15px;
  border-radius: 5px;
}

.body-left{
  color: #152F79;
  text-align: left;
  font-size: 15px;
  width: 35%;
  padding-left: 15px;
  padding-right: 0px;
  padding-top: 6px;
}

.body .body-left ul{
  padding-left: 19px;
}

.body .body-right ul{
  padding-left: 19px;
}

.body .body-right h5{
  padding-top: 5px;
}

@media screen and (max-width: 1092px) {
  .resume{
    width: 70%;
    margin: auto;
    box-shadow: 5px 5px 5px 5px #BDC8E9;
  }

  .header{
    background-color: #152F79;
    color: #ECEDF0;
    display:block;
    -webkit-align-items: center;
            align-items: center;

  }
  .photo{
    max-width: 143px;
    max-height: 143px;
    width: 50%;
    margin: auto;
  }
  .headerDetails{
    width: 67%;
    margin: auto;
    box-sizing: content-box;
    padding-left: 40px;
  }
}

@media screen and (max-width: 760px) {
  .resume{
    width: 90%;
    margin: auto;
    box-shadow: 5px 5px 5px 5px #BDC8E9;
  }

  .header{
    background-color: #152F79;
    color: #ECEDF0;
    display:block;
    -webkit-align-items: center;
            align-items: center;

  }
  .photo{
    max-width: 143px;
    max-height: 143px;
    width: 50%;
    margin: auto;
  }
  .headerDetails{
    width: 67%;
    margin: auto;
    box-sizing: content-box;
    padding-left: 40px;
  }
}

@media screen and (max-width: 530px) {
  .resume{
    width: 100%;
    box-shadow: 5px 5px 5px 5px #BDC8E9;
  }

  .header{
    background-color: #152F79;
    color: #ECEDF0;
    display:block;
    -webkit-align-items: center;
            align-items: center;

  }
  .photo{
    max-width: 143px;
    max-height: 143px;
    width: 50%;
    margin: auto;
  }
  .headerDetails{
    width: 67%;
    margin: auto;
    box-sizing: content-box;
    padding-left: 40px;
  }
}


.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

